.App {
    padding-top: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.title {
    text-align: center;
    font-size: 26px;
}

input {
    margin: 5px
}



.list-item {
    display: inline-block;
    word-wrap: break-word;    
}

.delete-button {
    float: right;
    padding-left: 5px;
    cursor: pointer;
}

.reminder-text {
    width: 150px;
    word-wrap: break-word;  
}



